
.text-body {
  position: relative;
  font-size: 16px;
  letter-spacing: 0.1em;
  width: 100%;
  word-break: break-all;
}
@media print {
.text-body strong {
      text-decoration: underline;
}
}
.text-body p {
    text-indent: 2em;
    margin: 0 0 1em 0;
    max-width: 100%;
    line-height: 1.7em;
}
.text-body.text-body__mobile p {
    text-indent: 1em;
}
.text-body.text-body__mobile blockquote p {
    text-indent: 0;
}
.text-body ul {
    padding-left: 2.5em;
}
.text-body ul li {
      margin-bottom: 0.3em;
      line-height: 1.5em;
}
.text-body.text-body__mobile ul {
    padding-left: 1.5em;
}
.text-body img {
    max-width: 96%;
    min-width: 0;
    width: auto;
    margin-left: -1.5em;
}
.text-body blockquote {
    border-left: 7px solid #afb3b4;
    margin: 0.5em 0 0.7em 0;
    color: #585c5d;
    background-color: #e1e1e1;
    padding: 0.2em 0 0.2em 0.4em;
}
.text-body blockquote p {
      text-indent: 0;
      margin: 0.2em 0 0.6em 0;
}
.text-body blockquote p.markdown-render--quote__--, .text-body blockquote p .markdown-render--quote__-- {
        text-align: right;
}
.text-body a {
    letter-spacing: 0;
}
.text-body pre {
    border: 1px dotted #2b2d2f;
    background-color: #e6e6e6;
    padding: 0.5em 1em;
    letter-spacing: 0;
}
.text-body pre code {
      background-color: inherit;
}
.text-body code {
    background-color: #f9f2f4;
    letter-spacing: 0;
}
.text-body .fa {
    text-indent: 0;
}
.text-body .text-body--pea-not {
    font-style: italic;
    text-decoration: line-through;
    margin-right: 0.2em;
}
.text-body .text-body--pea-detail {
    display: inline-block;
    text-indent: 0;
    font-style: normal;
    cursor: pointer;
    border: 1px solid #2b2d2f;
    padding: 0 0.175em;
    margin: 0 0.175em;
    line-height: 1.35em;
}
.text-body .text-body--pea-hidden {
    font-style: normal;
    color: #fff;
    background-color: #fff;
    border-bottom: 1px solid #2b2d2f;
}
.text-body .text-body--pea-hidden.text-body--pea-hidden__hidden {
      color: #fff !important;
}
.text-body .text-body--pea-hidden.text-body--pea-hidden__show, .text-body .text-body--pea-hidden:hover {
      color: inherit !important;
      border: none !important;
}
.text-body .text-body__mobile {
    letter-spacing: 1px;
}
.text-body--pea-tooltip-window {
  border: 1px solid #999b9e;
  -webkit-box-shadow: 1px 1px 6px #4c4c4c;
          box-shadow: 1px 1px 6px #4c4c4c;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #f4f4f4;
  z-index: 900;
}
.text-body--pea-tooltip-window .text-body--pea-tooltip-window--body {
    padding: 0.3em;
    width: 16rem;
}
.text-body--pea-tooltip-window .text-body--pea-tooltip-window--body.text-body {
      width: 32rem;
      max-width: 98vw;
}
.text-body--pea-tooltip-window .text-body--pea-tooltip-window--icon {
    font-size: 25px;
    position: absolute;
    bottom: 9px;
    right: 3rem;
}
.text-body--pea-tooltip-window .text-body--pea-tooltip-window--icon .fa:nth-child(1) {
      position: absolute;
      top: -1px;
      color: #f4f4f4;
      z-index: 200;
}
.text-body--pea-tooltip-window .text-body--pea-tooltip-window--icon .fa:nth-child(2) {
      color: #999b9e;
      z-index: 199;
      position: absolute;
}



