
.page-view.page-view__desktop {
  padding: 35px;
}
.page-view.page-view__desktop .profile-card .radar-img {
    margin-right: -30px;
}
.page-view.page-view__desktop > .block-decal {
    margin: 0 30px 30px;
}
.page-view.page-view__desktop > .block-collapsible {
    margin-bottom: 30px;
}
.page-view.page-view__desktop > .block-justify {
    margin-bottom: 60px;
}
.page-view.page-view__mobile {
  padding: 15px;
}
.page-view.page-view__mobile .article-header {
    margin-bottom: 20px;
}
.page-view.page-view__mobile > .block-decal {
    margin: 0 15px 40px;
}
.page-view.page-view__mobile > .block-collapsible {
    margin-bottom: 40px;
}
.page-view.page-view__mobile > .block-justify {
    margin-bottom: 20px;
}
@media only screen and (max-width: 330px) {
.page-view {
    padding-left: 2px !important;
    padding-right: 2px !important;
}
.page-view > .block-collapsible {
      margin-bottom: 40px;
      padding-left: 10px;
      padding-right: 10px;
}
}



